
import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { FactoryOrValue } from 'rxjs';
import { ApiFacultyService } from '../api-faculty.service';
import { Faculty } from '../faculty';
import { FacultyComponent } from '../faculty/faculty.component';




@Component({
  selector: 'app-details-faculty',
  templateUrl: './details-faculty.component.html',
  styleUrls: ['./details-faculty.component.css']
})
export class DetailsFacultyComponent implements OnInit {
  id: any;

  constructor(private _activatedRoute : ActivatedRoute,private _api:ApiFacultyService,private _route:Router) { }
  ngOnInit(): void {
    this.id = this._activatedRoute.snapshot.params.id;
    let ob = this._api.getFacultyById(this.id);
    ob.subscribe((res:any)=>{
    this.facultyDetail = res;
    });
    }

  facultyDetail : Faculty = new Faculty();

  deleteFaculty()
  {
    let ob = this._api.delete(this.id);
    ob.subscribe((res:any)=>{
      this._route.navigate(['faculty']);
    });
    
   
  }

  editFaculty()
  {
    this._route.navigate(['faculty/edit',this.id]);
  }

  returnfaculty(){
    this._route.navigate(['faculty']);
  }
 
}
