import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { ApiFacultyService } from '../api-faculty.service';
import { Faculty } from '../faculty';


@Component({
  selector: 'app-add-faculty',
  templateUrl: './add-faculty.component.html',
  styleUrls: ['./add-faculty.component.css']
})
export class AddFacultyComponent implements OnInit {

  facultyDetail : Faculty = new Faculty();
  id = 0;


  constructor(private _api:ApiFacultyService,private _route:Router,private _activetedroute:ActivatedRoute) { }

  ngOnInit(): void {
    this.id = this._activetedroute.snapshot.params.id;
    if(this.id==undefined)
    {
      this.id=0;
    }
    else{
      let ob = this._api.getFacultyById(this.id);
      ob.subscribe((res:any)=>{
      this.facultyDetail = res;
    });
    }

    }
  

  
    addEditFaculty(){
    

    if(this.id===0)
    {

    this._api.insert(this.facultyDetail).subscribe(()=>{

      this._route.navigate(['faculty']);
    })
    
  }
  else{
    this._api.update(this.facultyDetail,this.id).subscribe(()=>{

      this._route.navigate(['faculty']);
    })
  }
  }
  returnfaculty(){
    this._route.navigate(['faculty']);
  }
}
