import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { AddFacultyComponent } from './add-faculty/add-faculty.component';
import { AdduserComponent } from './adduser/adduser.component';
import { AuthGuard } from './auth.guard';
import { DetailsFacultyComponent } from './details-faculty/details-faculty.component';
import { FacultyComponent } from './faculty/faculty.component';
import { HomeComponent } from './home/home.component';
import { LoginComponent } from './login/login.component';
import { UserDetailComponent } from './user-detail/user-detail.component';
import { UserComponent } from './user/user.component';

const routes: Routes = [
 
  {path:"",component:LoginComponent},
  {path:"login",component:LoginComponent},
  {path :"home", component :HomeComponent, canActivate:[AuthGuard]},
  {path :"faculty", component : FacultyComponent, canActivate:[AuthGuard]},
  {path:"faculty/add", component:AddFacultyComponent, canActivate:[AuthGuard]},
  {path:"faculty/edit/:id", component:AddFacultyComponent, canActivate:[AuthGuard]},
  {path:"faculty/:id", component:DetailsFacultyComponent, canActivate:[AuthGuard]},
  {path:"user", component:UserComponent, canActivate:[AuthGuard]},
  {path:"user/add",component:AdduserComponent},
  {path:"user/edit/:id", component:AdduserComponent, canActivate:[AuthGuard]},
  {path:"user/:id",component:UserDetailComponent, canActivate:[AuthGuard]}
 
  
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
