import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { ApiUserService } from '../api-user.service';
import { User } from '../user';
// import { UserComponent } from '../user/user.component';
@Component({
  selector: 'app-user-detail',
  templateUrl: './user-detail.component.html',
  styleUrls: ['./user-detail.component.css']
})
export class UserDetailComponent implements OnInit {

  id: any;
  userDetail : User = new User();
  constructor(private _activatedRoute : ActivatedRoute, private _api:ApiUserService, private _route:Router) { }

  ngOnInit(): void {
    this.id = this._activatedRoute.snapshot.params.id;
    let ob = this._api.getuserById(this.id);
    ob.subscribe((res:any)=>{
      this.userDetail = res;
    });
  }

  returnuser(){
    this._route.navigate(['user']);
  }

  deleteUser(){
    let ob = this._api.delete(this.id);
    ob.subscribe((res:any)=>{
      this._route.navigate(['user']);
    });
  }

  editUser(){
   console.log (this._route.navigate(['user/edit',this.id]));
  }

}


