import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { ApiUserService } from '../api-user.service';
import { User } from '../user';

@Component({
  selector: 'app-adduser',
  templateUrl: './adduser.component.html',
  styleUrls: ['./adduser.component.css']
})
export class AdduserComponent implements OnInit {


    id:any;
    userDetail : User = new User();

  constructor(private _activatedRoute:ActivatedRoute,private _api:ApiUserService, private _route:Router) { }

  ngOnInit(): void {
  
    this.id=this._activatedRoute.snapshot.params.id;
        if(this.id==undefined){
          this.id=0;
        }
        else{
          let ob = this._api.getuserById(this.id);
          ob.subscribe((res:any)=>{
            this.userDetail=res;
          });

 
}
  }

  addEditUser(){
    if(this.id==0){
      let ob = this._api.insert(this.userDetail);
      ob.subscribe(()=>{
      this._route.navigate(['user']);
    });
    }
else{

     let ob = this._api.update(this.userDetail,this.id);
     ob.subscribe(()=>{
     this._route.navigate(['user']);
   });

   }
  }

  returnUser(){
         this._route.navigate(['user']);
       }

}